<template>
  <v-container id="login" class="fill-height justify-center" tag="section">
    <v-row justify="center">
      <v-col lg="11" sm="8" xl="7">
        <v-card class="elevation-4">
          <v-row>
            <v-col lg="7" class="info d-none d-md-flex align-center justify-center">
              <div class="d-none d-sm-block">
                <div class="d-flex align-center pa-10">
                  <div>
                    <h2
                      class="display-1 white--text font-weight-medium"
                    >老王帮你做交易</h2>
                    <h6
                      class="subtitle-1 mt-4 white--text op-5 font-weight-regular"
                    >老王帮你做交易.</h6>
                    <v-btn class="mt-4 text-capitalize" x-large outlined color="white">了解更多</v-btn>
                  </div>
                </div>
              </div>
            </v-col>
            <v-col lg="5">
              <div class="pa-7 pa-sm-12">
                <img src="@/assets/images/odklogo.png" />
                <h2 class="font-weight-bold mt-4 blue-grey--text text--darken-2">登陆</h2>
                <h6 class="subtitle-1">
                  还没有账户?
                  <a href="#" @click="gotoregister">注册</a>
                </h6>

                <v-form ref="form" v-model="valid" lazy-validation action="/dashboards/analytical">
                  <v-text-field
                    v-model="username"
                    :rules="emailRules"
                    label="用户名"
                    class="mt-4"
                    required
                    outlined
                  ></v-text-field>
                  <v-text-field
                    v-model="password"
                    :rules="passwordRules"
                    label="密码"
                    required
                    outlined
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'"
                  ></v-text-field>

                  <div class="d-block d-sm-flex align-center mb-4 mb-sm-0">
                    <v-checkbox
                      v-model="checkbox"
                      label="记住我?"
                      required
                    ></v-checkbox>
                    <div class="ml-auto">
                      <a href="javascript:void(0)" class="link" @click="logout">登出?</a>
                    </div>
                  </div>
                  <v-btn
                    :disabled="!valid"
                    color="info"
                    block
                    class="mr-4"
                    submit
                    @click="submit"
                  >登陆</v-btn>
                </v-form>
                
              </div>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { get} from 'vuex-pathify'

export default {
  name: "BoxedLoginHash",

  data: () => ({
    valid: true,
    password: "",
    show1: false,
    passwordRules: [
      v => !!v || "请输入密码",
      v => (v && v.length >= 6) || "密码长度必须大于6位"
    ],
    username:"",
    email: "",
    emailRules: [
      v => !!v || "请输入用户名",
      v => (v && v.length >= 4) || "用户名格式不正确，长度必须大于4位"
    ],
    checkbox: false
  }),
  computed: {
    isLogin:get('user/islogin'),
    },
  methods: {
    logout(){
      this.$store.dispatch('user/logout');
    },
    go_userinfo(){
        this.$router.push({path:"/user/userinfo"});
    },
    gotoregister(){
      this.$router.push({path:"/user/register"});
    },
    gotoresetpassword(){
      this.$router.push({path:"/user/resetpassword"});
    },
   
   

    submit() {
      this.$refs.form.validate();
      if (this.$refs.form.validate(true)) {
        let params ={username:this.username,password:this.password,piccode:this.piccode };
        // params ={username:this.username,password:this.password,piccode:this.piccode };
        this.$store.dispatch('user/logout');
        this.$store.dispatch('user/login',params);
        // this.$router.push({ path: "/dashboards/analytical" });
      }
    }
  },
  watch:{
      isLogin(){
          if (this.isLogin){
              this.go_userinfo()
          }
      }
    }
};
</script>
